<script>
import CustomerView from "../components/CustomerEditableComponent.vue";
import PersonView from "../components/PersonEditableComponent.vue";
import ClerkView from "../components/ClerkEditableComponent.vue";
import ReservationView from "../components/ReservationComponent.vue";
import VehicleView from "../components/VehicleComponent.vue";
import InstructionsView from "../components/BuilderInstructionsViewComponent.vue";
import ReservationSystemView from "../components/ReservationSystemComponent.vue";
import ClassView from "../components/FactoryDiagramComponent.vue";
import TestDriverComponent from "@/components/BuilderTestDriverComponent.vue";
import PersonBuilderComponent from "@/components/PersonBuilderComponent.vue";

export default {
  components: {
    CustomerView,
    PersonView,
    ClerkView,
    ReservationView,
    VehicleView,
    ReservationSystemView,
    InstructionsView,
    ClassView,
    TestDriverComponent,
    PersonBuilderComponent

  },
  data() {
    return {
      componentSelect: "InstructionsView",

      builderTestComplete: false,
      execEditableComplete: false,
      customerEditableComplete: false,
      personBuilderComplete: false,
      personEditableComplete: false,
      instructionsComplete: false,

    };
  },
  methods: {
    //4, 2, 1
    builderTestCallback(value) {
      console.log("1Test driver triggered", value);
      this.builderTestComplete = true;
    },
    execEditableCallback(value) {
      console.log("2Test driver triggered", value);
      this.execEditableComplete = true;
    },
    customerEditableCallback(value) {
      console.log("3Test driver triggered", value);
      this.customerEditableComplete = true;
    },
    personBuilderCallback(value) {
      console.log("4Test driver triggered", value);
      this.personBuilderComplete = true;
    },
    personEditableCallback(value) {
      console.log("5Test driver triggered", value);
      this.personEditableComplete = true;
    },
    instructionsReadCallback(value) {
      console.log("Builder instructions done", value)
      this.componentSelect = "PersonBuilderComponent"
    }
  },
};
</script>

<template>
  <main class="app">
    <!--    :style="componentSelect === 'InstructionsView' ? 'background-color: #55acee' : ''"-->
    <button @click="componentSelect = 'InstructionsView'"
            :style="componentSelect === 'InstructionsView' ? 'background-color: #55acee' : ''">1. Instructions
    </button>
    <button @click="componentSelect = 'ClassView'"
            :style="componentSelect === 'ClassView' ? 'background-color: #55acee' : ''">2. Class diagram
    </button>
    <button @click="componentSelect = 'PersonBuilderComponent'"
            :style="componentSelect === 'PersonBuilderComponent' ? 'background-color: #55acee' : ''">3. Person Builder
      Class
    </button>
    <button @click="componentSelect = 'PersonView'"
            :style="componentSelect === 'PersonView' ? 'background-color: #55acee' : ''" :disabled="!personBuilderComplete">4. Person Class
    </button>
    <button @click="componentSelect = 'CustomerView'"
            :style="componentSelect === 'CustomerView' ? 'background-color: #55acee' : ''" :disabled="!personEditableComplete">5. Customer Class
    </button>
    <button @click="componentSelect = 'ClerkView'"
            :style="componentSelect === 'ClerkView' ? 'background-color: #55acee' : ''" :disabled="!customerEditableComplete">6. Exec Class
    </button>
    <button @click="componentSelect = 'ReservationView'"
            :style="componentSelect === 'ReservationView' ? 'background-color: #55acee' : ''">
      7. Reservation Class
    </button>
    <button @click="componentSelect = 'VehicleView'"
            :style="componentSelect === 'VehicleView' ? 'background-color: #55acee' : ''">8. Vehicle Class
    </button>
    <button @click="componentSelect = 'ReservationSystemView'"
            :style="componentSelect === 'ReservationSystemView' ? 'background-color: #55acee' : ''">
      9. Rental System Class
    </button>
    <button @click="componentSelect = 'TestDriverComponent'"
            :style="componentSelect === 'TestDriverComponent' ? 'background-color: #55acee' : ''" :disabled="!execEditableComplete">
      10. Test Driver Class
    </button>
    

    <KeepAlive>
      <component
          v-bind:is="componentSelect"
          @builder-test-complete="builderTestCallback"
          @clerk-editable-complete="execEditableCallback"
          @customer-editable-complete="customerEditableCallback"
          @person-builder-complete="personBuilderCallback"
          @person-editable-complete="personEditableCallback"
          @instructions-read="instructionsReadCallback"
      />
    </KeepAlive>
  </main>
  <!--eslint-disable-next-line vue/no-multiple-template-root-->
  <footer
      v-show="(builderTestComplete && execEditableComplete && customerEditableComplete && personBuilderComplete && personEditableComplete)">
    <div id="buttons" class="relative">
      <button class="navitem" @click="$router.push('/screen-five')">Next</button>
    </div>
  </footer>
</template>

<style>
.app {
  background-color: #f9fafe;
  min-height: 1024px;
}

button {
  margin-top: 30px;
  border: none;
  padding: 20px;
  margin-left: 5px;
  background-color: #2c3e50;
  color: white;
}

button:first-of-type {
  margin-left: 30px;
}

button:disabled {
  background-color: #7c8e9d;
}

</style>
 